body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

* {
    font-family: "Verdana", cursive, sans-serif;
    color: #ffffff;
}

body {
    background-color: #7cc6fe;
}

.reset {
    width: 25%;
}

.reset button {
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    margin-bottom: 15px;
    background-color: rgba(37, 45, 74, .3);
    border-radius: 10px;
    display: block;
    padding: 5px;
    align-items: center;
    border: 1px solid rgba(35, 70, 104, .3);
    cursor: pointer;
}

.outer {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.app {
    background-color: #252d4a;
    width: 300px;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: inline-block;
    justify-content: space-evenly;
}
.image{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: justify;
    position: relative;
    border: 1px;
}
.answers{
    display: flex;
    font-size: 24px;
    align-items: center;
}

.score-section {
    display: flex;
    font-size: 24px;
    align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
    width: 100%;
    position: relative;
}

.question-count {
    margin-bottom: 20px;
    text-align: center;
}

.question-count span {
    font-size: 20px;
}

.question-text {
    margin-bottom: 12px;
}

.timer-text {
    background: rgb(230, 153, 12);
    padding: 15px;
    margin-top: 20px;
    margin-right: 20px;
    border: 5px solid rgb(255, 189, 67);
    border-radius: 15px;
    text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

button {
    width: 100%;
    font-size: 16px;
    color: #ffffff;
    margin-bottom: 15px;
    background-color: #252d4a;
    border-radius: 15px;
    display: block;
    padding: 5px;
    align-items: center;
    border: 5px solid #234668;
    cursor: pointer;
}

.correct {
    background-color: #2f922f;
}

.incorrect {
    background-color: #ff3333;
}

button:hover {
    background-color: #555e7d;
}

button:focus {
    outline: none;
}

button svg {
    margin-right: 5px;
}